.App {
  font-family: sans-serif;
  text-align: center;
  background-color: #0a33fb;
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  font-family: 'Overpass', sans-serif;
}

.container{
  display: flex;
  justify-content: flex-start;
  flex-flow: column;
  align-items: left;
  margin: 30px auto;
  padding: 20px;
  text-align: left;
  /* max-width: 500px; */
}


h1{
  color: #ff3bff;
  font-weight: 700 !important;
  font-size: 100px !important;
}
.description{
  color: #eee6ee;
  font-size: 15px !important;
  font-weight: 200;
  width: 350px;
  padding-right: 10px;
}

@media screen and (max-width: 736px) {
  h1 {
    font-size: 60px !important;
  }
}






